.ba-footer {
  background: #9f9acf;
  padding: 5%;
  height: 185px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  color: #fff;
  text-align: center;
}

.ba-footer__social-links {
  display: flex;
}

@media all and (max-width: 665px) {
  .ba-footer {
    justify-content: center;
  }
}
